*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body,
#root {
  font-size: 10px;
  font-family: 'Orbitron', sans-serif;
  margin: 0;
  padding: 0;
  letter-spacing: 1.8px;
  background-color: #191123;
}

@media screen and (max-width: 768px) {
  html,
  body,
  #root {
    font-size: 8px !important;
  }
}

@media screen and (max-width: 576px) {
  html,
  body,
  #root {
    font-size: 7px !important;
  }
}

.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 200ms, transform 200ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 200ms, transform 200ms;
}

.fadeslow-enter {
  opacity: 0;
}
.fadeslow-enter-active {
  opacity: 1;
  transition: opacity 2500ms, transform 2500ms;
}
.fadeslow-exit {
  opacity: 1;
}
.fadeslow-exit-active {
  opacity: 0;
  transition: opacity 2500ms, transform 2500ms;
}

.menu-primary-enter {
  opacity: 0;
}

.menu-primary-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in;
}

.menu-primary-exit {
  opacity: 1;
}

.menu-primary-exit-active {
  opacity: 0;
  transition: opacity 250ms ease-in;
}

.page {
  position: absolute;
  left: 15px;
  right: 15px;
}

.page-enter {
  opacity: 0;
  transform: scale(0.8);
}

.page-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 200ms, transform 200ms;
}

.page-exit {
  opacity: 1;
  transform: scale(1);
}

.page-exit-active {
  opacity: 0;
  transform: scale(0.8);
  transition: opacity 200ms, transform 200ms;
}

// Custom for react-slick
.slick-initialized .slick-slide.slick-active {
  z-index: 1;
}

// Custom for react toastify
.Toastify__toast {
  padding: 15px 15px 20px 15px !important;
}

.Toastify__toast--success {
  background: none !important;
  background-image: url('./assets/images/toastify-success-background.png') !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-size: contain !important;
}

.Toastify__toast--error {
  background: none !important;
  background-image: url('./assets/images/toastify-error-background.png') !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-size: contain !important;
}

.Toastify__toast--warning {
  background: none !important;
  background-image: url('./assets/images/toastify-warning-background.png') !important;
  background-repeat: no-repeat !important;
  background-position: center !important;
  background-size: contain !important;
}

.Toastify__close-button {
  margin-top: 2px !important;
}

.Toastify__toast-body {
  font-weight: 700;
  font-size: 1.2rem;
  line-height: 24px;
  letter-spacing: 1px;
}
